.ds-menu-lateral {
  padding: 20px 40px 40px;
  background: #f8f8f8;
  box-shadow: 0 6px 3px 0 rgba(0, 0, 0, 0.16);
}

.ds-menu-lateral ul, .ds-menu-lateral ul li {
  list-style: none;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.ds-menu-lateral ul li ul li a {
  padding-left: 55px;
}

.ds-menu-lateral ul li a {
  font-family: "RawlineMedium", Arial, sans-serif;
  color: #333;
  font-size: 16px;
  padding: 14px 25px;
  border-bottom: 1px solid #C5D4EB;
}

.ds-menu-lateral ul li a:hover {
  color: #fff;
  background: #1351B4;
  text-decoration: none;
}

.ds-menu-lateral h1, .ds-menu-lateral h2, .ds-menu-lateral h3, .ds-menu-lateral h4 {
  font-family: "RawlineSemiBold", Arial, sans-serif;
  color: #1351b4;
}

.ds-menu-lateral h2 {
  font-family: "RawlineSemiBold", Arial, sans-serif;
  height: 68px;
  color: #0090a8;
  margin: 0 -40px 40px -40px;
  padding: 24px 0 24px 40px;
  font-size: 14px;
  background: #e5e5e5;
  cursor: pointer;
}

.ds-menu-lateral h2:hover i {
  background: #69c6d1;
}

.ds-menu-lateral h2 a {
  text-decoration: none;
}

.ds-menu-lateral h2 i {
  width: 32px;
  height: 60px;
  line-height: 61px;
  text-align: center;
  float: right;
  margin-right: 4px;
  margin-top: -20px;
  display: inline-block;
  background: #aad4d9;
  transform: rotate(180deg);
  color: #fff;
}

.ds-menu-lateral h3 {
  font-family: "RawlineSemiBold", Arial, sans-serif;
  font-size: 16px;
  padding-bottom: 8px;
  color: #1351b4;
  padding: 14px 25px;
  margin-top: 0;
  margin-bottom: 0;
  border-bottom: 1px solid #333;
  text-transform: uppercase;
}

.ds-menu-lateral h3:hover {
  color: #fff;
  background: #1351B4;
}

.ds-menu-lateral h3:hover::after {
  color: #fff;
}

.ds-menu-lateral h3::after {
  font-family: Font Awesome\ 5 Free;
  font-size: 14px;
  font-weight: 900;
  content: "\f077";
  position: absolute;
  right: 60px;
}

.ds-menu-lateral h4 {
  font-family: "RawlineMedium", Arial, sans-serif;
  font-size: 16px;
  color: #333;
  font-weight: normal;
  padding: 14px 25px;
  margin-top: 0;
  margin-bottom: 0;
  border-bottom: 1px solid #C5D4EB;
}

.ds-menu-lateral h4:hover {
  color: #fff;
  background: #1351B4;
}

.ds-menu-lateral h4:hover::after {
  color: #fff;
}

.ds-menu-lateral h4::after {
  font-family: Font Awesome\ 5 Free;
  font-size: 16px;
  font-weight: 900;
  content: "\f077";
  position: absolute;
  right: 60px;
  color: #1351b4;
}

.hidemenu {
  position: relative;
  left: -30px;
}

.hidemenu h3, .hidemenu ul, .hidemenu h5 {
  display: none;
}

.label-mob-opened {
  display: inline;
}

.label-mob-closed,
.hidemenu .label-mob-closed,
.hidemenu .label-mob-opened {
  display: none;
}

@media (max-width: 992px) {
  .ds-menu-lateral {
    height: auto;
    min-height: 68px;
  }
  .ds-menu-lateral h2 i {
    width: 60px;
    height: 32px;
    line-height: 36px;
    margin-right: -10px;
    margin-top: -6px;
    transform: rotate(-90deg);
  }
  .ds-menu-lateral h3 {
    display: none;
  }
  .ds-menu-lateral h5 {
    display: none;
  }
  .ds-menu-lateral ul {
    display: none;
  }
  .hidemenu .ds-menu-lateral h2 i {
    height: 32px;
    line-height: 32px;
    margin-right: -10px;
    margin-top: -6px;
    transform: rotate(90deg);
  }
  .hidemenu .ds-menu-lateral h3 {
    display: block;
  }
  .hidemenu .ds-menu-lateral ul {
    display: block;
  }
}

@media (max-width: 992px) {
  .label-mob-closed,
  .hidemenu .label-mob-opened {
    display: none;
  }
  .label-mob-opened,
  .hidemenu .label-mob-closed {
    display: inline;
  }
}

a.menu-item {
  display: block;
}
